.post_item {
    .copy-wrapper {
        margin-top: -150px;
        h1 {
            margin-bottom: 0;
        }
        p {
            margin-top: 2rem;
            font-size: 1rem;
            line-height: 1.5;
            font-family: raleway next, raleway, sans-serif;
        }
        ul {
            margin-top: 2rem;
            font-family: raleway next, raleway, sans-serif;
            li {
                font-size: 1rem;
                margin-bottom: 1rem;
                line-height: 1.5;
                font-family: raleway next, raleway, sans-serif;
            }
        }
    }
    .case-study-sidebar {
      strong {
        font-weight: 500;
      }
    }

    // .case-study-info {
    //     background-color: rgba(0, 27, 68, .75);
    //     position: relative;
    //     top: 50%;
    //     -webkit-transform: translateY(-50%);
    //     -ms-transform: translateY(-50%);
    //     transform: translateY(-50%);
    // }

    .quote {
        font-size: 12px;
    }

    .double-image img {
        width: 300px;
        height: 300px;
    }

    .embed-container {
        position: relative;
        padding-bottom: 56.25%;
        height: 0;
        overflow: hidden;
        max-width: 100%;
    }

    .embed-container iframe,
    .embed-container object,
    .embed-container embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

